@import "../../common/styles/sass/variables";

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  position: relative;
  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    width: 75%;
    margin: 30px auto;
    padding: 30px;
    .greeting {
      padding: 20px;
      p {
        margin: 0;
        font-weight: 400;
        font-size: 30px;
        font-family: 'Roboto', sans-serif;
      }
      .name {
        p {
          font-weight: 400;
          font-size: 30px;
          line-height: 1.4;
          font-family: 'Roboto', sans-serif;
          font-stretch: normal;
          letter-spacing: 2px;
        }
        & span:nth-child(2),
        & span:nth-child(3) {
          position: relative;
          z-index: 0;

          display: inline-block;
          padding-right: 15px;

          font-weight: 600;
          font-size: 50px;
          line-height: 1.3;
          font-family: 'Roboto', sans-serif;
          font-stretch: normal;
          &:after {
            content: "";
            position: absolute;
            bottom: 0;
            left: 2px;
            z-index: -1;
            width: 206px;
            height: 20px;
            background-color: $primaryColor;
          }
          &:nth-child(3):after {
            width: 172px;
          }
        }
      }
    }

    .photo {
      position: relative;
      z-index: 0;

      margin-top: 30px;
    }
  }
}


.name span:nth-child(3):after {
  width: 175px;
}


h1 {
  font-weight: 400;
  font-size: 30px;
  line-height: 1.5;
  font-family: 'Roboto', sans-serif;
}


.photo:before {
  content: "";
  position: absolute;
  top: -20px;
  left: 15px;
  z-index: -1;

  width: 325px;
  height: 460px;

  border: 5px solid $primaryColor;
}

.myPhoto {
  max-height: 430px;
}

@media screen and (max-width: 1000px) {
  .myPhoto {
    max-height: 300px;
  }
  .photo:before {
    width: 220px;
    height: 330px;
  }
}
